import { SELECTORS, ELEMENT_SELECTORS, getSel } from '../../static/js/utils/selectorConstants.js';

describe('selectorConstants immutability & referential integrity', () => {
  test('SELECTORS object is frozen', () => {
    expect(Object.isFrozen(SELECTORS)).toBe(true);
  });

  test('ELEMENT_SELECTORS object is frozen', () => {
    expect(Object.isFrozen(ELEMENT_SELECTORS)).toBe(true);
    expect(Object.isFrozen(ELEMENT_SELECTORS.CHAT)).toBe(true);
    expect(Object.isFrozen(ELEMENT_SELECTORS.KB)).toBe(true);
  });

  test('getSel helper prefixes ids correctly', () => {
    const id = 'chatUIContainer';
    expect(getSel(id)).toBe(`#${id}`);
    // Ensure proxy returns same prefixed value
    expect(SELECTORS.chatUIContainer).toBe(`#${id}`);

    // Newly added Week-2 selector
    expect(getSel('indexKbCheckbox')).toBe('#indexKbCheckbox');
  });

  test('KB aliases map to canonical ids', () => {
    expect(ELEMENT_SELECTORS.KB.activeSection).toBe('knowledgeStatus');
    expect(ELEMENT_SELECTORS.KB.inactiveSection).toBe('knowledgeBaseInactive');
  });
});
